                    padding: [10, 10, 0, 10],
                    color: '#ffffff',
                  },
                },
              },
              emphasis: {
                show: true,
              },
            },
            itemStyle: {
              color: '#feae21', //
            },
          },
          {
            type: 'effectScatter',
            coordinateSystem: 'geo',
            zlevel: 1,
            data: [],
            symbolSize: 14,
            label: {
              normal: {
                show: true,
                formatter: function (params) {
                  return (
                    '{fline|' +
                    '  ' +
                    params.data.telphone +
                    '}\n{tline|' +
                    params.data.address +
                    '}'
                  );
                },
                position: 'top',
                backgroundColor: this.getRandomColor(),
                padding: [0, 0],
                borderRadius: 3,
                lineHeight: 32,
                color: '#ffffff',
                rich: {
                  fline: {
                    padding: [0, 10, 10, 10],
                    color: '#ffffff',
                  },
                  tline: {
                    padding: [10, 10, 0, 10],
                    color: '#ffffff',
                  },
                },
              },
              emphasis: {
                show: true,
              },
            },
            itemStyle: {
              color: '#e93f42',
            },
          },
          // 
          {
            type: 'map', //
            mapType: 'china', //
            aspectScale: 0.85, //scale
            layoutCenter: ['50%', '50%'], //
            layoutSize: '100%', //
            zoom: 1.4, //
            scaleLimit: {
              //
              min: 1,
              max: 2,
            },
            data: [data[currentDataIndex]],
            itemStyle: {
              // 
              normal: {
                areaColor: '#0c274b',
                borderColor: '#1cccff',
                borderWidth: 1.5,
              },
              emphasis: {
                areaColor: '#02102b',
              },
            },
