              show: true,
              color: '#fff', // 
              align: 'center',
              verticalAlign: 'center',
            },
            emphasis: {
              //
              label: {
                show: true,
                color: '#fff', // 
                align: 'center',
                verticalAlign: 'center',
              },
            },
          },
          {
            //
            name: '',
            type: 'scatter',
            coordinateSystem: 'geo',
            data: [
              {
                name: '',
                value: [116.24, 41.55, 100],
              },
            ],
            symbol: iconGQ,
            symbolSize: 20,
            label: {
              normal: {
                show: false,
              },
              emphasis: {
                show: false,
              },
            },
          },
