              normal: {
                show: false, //
                position: 'left',
                formatter: '{b}',
                color: '#b3e2f2',
                fontWeight: 'bold',
                fontSize: 12,
              },
            },
          },
        ],
      };

      this.myChart.setOption(option);
      this.timer = setInterval(() => {
        var validProvinces = ['', '']; // 
        var runidx = Math.floor(Math.random() * validProvinces.length); // 
        // var runidx = 1; // 
        var typeidx = Math.floor(Math.random() * 1);

        var dataidx = 1;
        if (validProvinces[runidx] === '') {
          dataidx = 0; //  chinaDatas 
        } else if (validProvinces[runidx] === '') {
          dataidx = 1; //  chinaDatas 
        }
        var ranval = Math.floor(Math.random() * 10);
        //
        this.chinaDatas[dataidx].value =
          this.chinaDatas[dataidx].value + ranval;
        var valarr = this.geoCoordMap[validProvinces[runidx]];
        valarr.push(ranval);

        //
        option.series[typeidx].data = [
          {
            name: '',
            telphone: this.getTel(runidx),
            address: this.getAddress(dataidx, typeidx),
            value: valarr,
          },
        ];

        this.myChart.setOption(option, true);
      }, 3000);
      // window.addEventListener('resize', function () {
      //   this.myChart.resize();
      // });
    },
    /**
     * 
     */
    onResize() {
      if (this.myChart) {
        this.myChart.resize();
      }
    },
    // 
    getRandomColor() {
      //  RGB 
      var r = Math.floor(Math.random() * 256);
      var g = Math.floor(Math.random() * 256);
      var b = Math.floor(Math.random() * 256);
      return 'rgba(' + r + ',' + g + ',' + b + ', 0.9)';
    },
    //
    getTel(index) {
      let tel = [
        '',
        '',
      ];
      if (index === 0) {
        return tel[0];
      } else {
        return tel[1];
      }
    },

    getAddress(num) {
      var addstr = '';
      if (this.chinaDatas[num].name === '') {
        addstr =
          '' +
          this.chinaDatas[num].name +
          '205599';
      } else {
        addstr =
          '' +
          this.chinaDatas[num].name +
          '33512314B';
      }
      return addstr;
    },
  },
  computed: {
    pageHeight() {
      return window.innerHeight;
    },
  },
  mounted() {
    /**
     * 1
     * 2echarts
     */
    this.getMapData();
    this.startCarousel();
    // this.initChartTime(); //
    // this.startAutoPlay();
    this.renderChart();
    this.gnGwChart();
    this.initWorldData();
    this.checkScroll();
  },
  beforeDestroy() {
    clearInterval(this.timer); // 
    // window.removeEventListener('resize', this.onResize()); // 
    // if (this.myChartTime) {
    //   this.myChartTime.dispose();
