<template>
  <div ref="chart"></div>
</template>

<script>
export default {
  data() {
    return {
      data1: [
        [20, 50, '', '#BF54Fb', 65, 1600],
        [25, -45, '', '#4554Fb', 65, 960],
        [-25, 45, '', '#5b6dc4', 65, 180],
        [-25, -45, '', '#f66dc4', 65, 44],
      ],
    };
  },
  mounted() {
    this.renderChart();
  },
  methods: {
    renderChart() {
      const myChart = this.$echarts.init(this.$refs.chart);
      const option = {
        xAxis: [
          {
            show: false,
            type: 'value',
            min: -50,
            max: 50,
          },
        ],
        yAxis: [
          {
            min: -50,
            max: 50,
            show: false,
          },
        ],
        tooltip: {
          show: false,
        },
        series: [
          {
            zlevel: 20,
            type: 'scatter',
            symbol: 'circle',
            symbolSize: 110,
            label: {
              normal: {
                show: true,
                fontSize: 20,
                formatter: function (param) {
                  return param.data[2];
                },
              },
            },
            itemStyle: {
              normal: {
                color: '#5aa8ee',
              },
            },
            data: [[0, 0, '']],
          },
          {
            zlevel: 20,
            type: 'scatter',
            symbol: 'circle',
            symbolSize: 1,
            label: {
              normal: {
                show: false,
                fontSize: 20,
              },
            },
            itemStyle: {
              normal: {
                color: '#5aa8ee',
              },
            },
          },
          {
            zlevel: 20,
            type: 'scatter',
            symbol: 'circle',
            symbolSize: function (param) {
              return param[4];
            },
            itemStyle: {
              color: function (param) {
                return param.data[3];
              },
            },
            label: {
              show: true,
              textStyle: {
                fontSize: 12,
              },
              formatter: function (param) {
                return param.data[2];
              },
            },
            data: this.data1,
          },
        ],
      };
      myChart.setOption(option);
    },
  },
};
</script>
